import React from "react";
import styled from "styled-components";
import { useUserVideos } from "../../common/hooks/useUserVideos";

const VideoManagement = ({ className }) => {
  const { data } = useUserVideos();
  return (
    <section className={`${className} user__management__video col-12 col-lg-9`}>
      <header className="fs__title fw-bolder">Video của bạn</header>
      {data &&
        data?.map((video) => {
          const { name_i, number_order } = video;
          return (
            <div className="card">
              <div className="card__video">
                <img src="/images/314a50ada9025a5c0313.jpg" alt="" />
              </div>
              <div className="card__body">
                <p className="card__body__title fs__text fw-bolder">
                  {name_i} {number_order}
                </p>
                <p className="card__body__date fs__smallest">
                  Xuất ngày <span>1/1/1111</span>
                </p>
              </div>
            </div>
          );
        })}
      <div className="card">
        <div className="card__video">
          <img src="/images/314a50ada9025a5c0313.jpg" alt="" />
        </div>
        <div className="card__body">
          <p className="card__body__title fs__text fw-bolder">Đường đời 1</p>
          <p className="card__body__date fs__smallest">
            Xuất ngày <span>1/1/1111</span>
          </p>
        </div>
      </div>
      <div className="card">
        <div className="card__video">
          <img src="/images/314a50ada9025a5c0313.jpg" alt="" />
        </div>
        <div className="card__body">
          <p className="card__body__title fs__text fw-bolder">Đường đời 1</p>
          <p className="card__body__date fs__smallest">
            Xuất ngày <span>1/1/1111</span>
          </p>
        </div>
      </div>
      <div className="card">
        <div className="card__video">
          <img src="/images/314a50ada9025a5c0313.jpg" alt="" />
        </div>
        <div className="card__body">
          <p className="card__body__title fs__text fw-bolder">Đường đời 1</p>
          <p className="card__body__date fs__smallest">
            Xuất ngày <span>1/1/1111</span>
          </p>
        </div>
      </div>
      <div className="card">
        <div className="card__video">
          <img src="/images/314a50ada9025a5c0313.jpg" alt="" />
        </div>
        <div className="card__body">
          <p className="card__body__title fs__text fw-bolder">Đường đời 1</p>
          <p className="card__body__date fs__smallest">
            Xuất ngày <span>1/1/1111</span>
          </p>
        </div>
      </div>
      <div className="card">
        <div className="card__video">
          <img src="/images/314a50ada9025a5c0313.jpg" alt="" />
        </div>
        <div className="card__body">
          <p className="card__body__title fs__text fw-bolder">Đường đời 1</p>
          <p className="card__body__date fs__smallest">
            Xuất ngày <span>1/1/1111</span>
          </p>
        </div>
      </div>
    </section>
  );
};

export default styled(VideoManagement)`
  &.user__management {
    &__video {
      /* width: 60%; */
      .card {
        margin-top: 20px;
        display: flex;
        flex-direction: row;
        background-color: ${(props) => props.theme.colorCard};
        border-radius: 16px;
        padding: 10px 20px 10px 10px;
        &__video {
          width: 150px;
          height: 100px;
          margin-right: 20px;
          img {
            border-radius: 16px;
            width: 100%;
            height: 100%;
            object-fit: cover;
          }
        }
        &__body {
          display: flex;
          flex-direction: column;
          p:first-child {
            margin-bottom: 5px;
          }
        }
      }
    }
  }
`;
